<template>
  <div class="picture-carousel" id="pan-header-picture">
    <v-carousel hide-delimiters cycle interval=6000 :show-arrows="false" height="600">
      <v-carousel-item
          v-for="(item,i) in panHeaderImages"
          :key="i"
          :src="item.src"
      >
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

<script>
export default {
  name: "mobileBodyPictureViewer",
  data() {
    return {
      panHeaderImages: this.importAllImages()
    };
  },
  methods: {
    importAllImages() {
      const images = [];
      const context = require.context(
          "../../../assets/pictures/pan-header", // 修改为当前相对路径
          false,                              // 不递归子文件夹
          /\.(png|jpe?g|webp)$/i              // 匹配图片文件类型
      );

      context.keys().forEach((key) => {
        images.push({ src: context(key) });
      });

      return images;
    }
  }
}
</script>

<style scoped>
div.picture-carousel{
  z-index: -1;
}

</style>